import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CampaignService {
  url = 'http://swagger.dashboard.api.spotcheck.airplaycontrol.com:10080/api';

  constructor(private http: HttpClient) { }
  
  getCampaignsList() {
    return this.http.get(this.url + `/Campaigns`);
  }
  getCampaign(uuid) {
    return this.http.get(this.url + `/Campaigns/${uuid}`);
  }
  getPlaysStatistics(){
    return this.http.get(this.url + `/Plays/Statistics`);
  }
  getCompaignStatistics(uuid){
    return this.http.get(this.url + `/Campaigns/${uuid}/Statistics`);
  }
  getCompaignFlights(uuid){
    return this.http.get(this.url + `/Campaigns/${uuid}/Flights`);
  }
  getCompaignPlaysStations(uuid){
    return this.http.get(this.url + `/Campaigns/${uuid}/Plays/Stations`);
  }
  getCompaignPlaysStationsWithPlaysCount(uuid){
    return this.http.get(this.url + `/Campaigns/${uuid}/Plays/StationsWithPlaysCount`);
  }
  // createWorkflow(body) {
  //   return this.http.post(this.url + `/workflows`, body);
  // }
  // updateWorkflow(workflowId, body) {
  //   return this.http.patch(this.url + `/workflows/${workflowId}`, body);
  // }

  // deleteWorkflow(workflowId) {
  //   return this.http.delete(this.url + `/workflows/${workflowId}`);
  // }

}
