import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Track } from 'ngx-audio-player';

@Component({
  selector: 'app-modal-video',
  templateUrl: './modal-video.component.html',
  styleUrls: ['./modal-video.component.scss']
})
export class ModalVideoComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<ModalVideoComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {
    console.log(data);
  }

  type;
  list;

  msbapTitle;
  msbapAudioUrl;
  msbapDisplayTitle;
  msDisplayVolumeControls;

  msaapDisplayTitle = true;
  msaapDisplayPlayList = true;
  msaapPageSizeOptions = [2, 4, 6];
  msaapDisplayVolumeControls = true;

  // Material Style Advance Audio Player Playlist
  msaapPlaylist: Track[] = [
    {
      title: 'Audio One Title',
      link: 'assets/examples/audio/y2mate.com-gotye_somebody_that_i_used_to_know_feat_kimbra_official_music_video_8UVNT4wvIGY.mp3'
    },
    {
      title: 'Audio Two Title',
      link: 'assets/examples/audio/y2mate.com-gotye_somebody_that_i_used_to_know_feat_kimbra_official_music_video_8UVNT4wvIGY.mp3'
    },
    {
      title: 'Audio Three Title',
      link: 'assets/examples/audio/y2mate.com-gotye_somebody_that_i_used_to_know_feat_kimbra_official_music_video_8UVNT4wvIGY.mp3'
    },
  ];

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    if (!this.data.list) {
      this.msbapTitle = this.data.title;
      this.msbapAudioUrl = this.data.source;
      this.type = this.data.type;
      this.list = this.data.list;
      this.msbapDisplayTitle = true;
      this.msDisplayVolumeControls = true;
    }

  }

}
